.modal {
  position: fixed;
  top: 25em;
  transform: translate(-50%, -50%);
  z-index: 1000;
  background-color: white;
  margin: auto 50%;
  border-radius: 1em;
}
.overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.226);
}
